/* 헤더 작업 */
.header {
  box-sizing: border-box;
  color: black;
  position: fixed;
  padding: 20px 40px;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.headerColumn {
  display: flex;
}
.headerColumn__item {
  margin: 2px;
}

/* 바디 */
.homeBody {
  width: 100%;
}

/* main */

.homeMain {
  margin-top: 10em;
}
